import { Component, OnInit, Output, EventEmitter } from "@angular/core";
import { InvAgentPlanModel, ActionConfig } from "shared/models";
import { InvAgentPlanService } from "shared/services/";
import { getUpdateFilter } from "shared/functions";
import { AlertModel } from "shared/models";
import { AlertService, EnvService } from "shared/services/";

import {
  textDateToObjectDate,
  objectDateToTextDate
} from 'shared/functions';

import * as moment from "moment";

@Component({
  selector: "app-crud-inv-agent-plan",
  templateUrl: "./crud-inv-agent-plan.component.html",
  styleUrls: ["./crud-inv-agent-plan.component.scss"]
})
export class CrudInvAgentPlanComponent implements OnInit {
  // @Output() newCreateRecord: EventEmitter<any> = new EventEmitter();
  @Output() mainAnswer: EventEmitter<any> = new EventEmitter();

  // MAIN VARIABLES
  alertMessage: AlertModel; 
  env;
  error_detected = false;
  error_message;

  selection: InvAgentPlanModel;
  action: ActionConfig;

  rows;
  rows_original;
  selected: [{ selected: InvAgentPlanModel }];
  query;

  rowsInTableList;
  numberOfRowsInTable;

  show_data;
  show_datatable;
  show_new_button;
  show_selected_button;

  findInList;
  filterFieldList;

  selectedFilterField;
  

  excel_subtitle;

  selectedDate = new Date();
  bsValue = new Date();

  // INITIALIZATION
  constructor(
    private invAgentPlanService: InvAgentPlanService,
    private alertService: AlertService,
    private envService: EnvService
  ) {
    this.alertMessage = new AlertModel();

    this.show_data = true;
    this.show_datatable = true;
    this.show_new_button = true;
    this.show_selected_button = false;

    this.selection = new InvAgentPlanModel();
    this.action = new ActionConfig();
    
    this.excel_subtitle = "agente";

    this.selected = [{ selected: new InvAgentPlanModel() }];
    this.rowsInTableList = [
      { id: 1, value: 1 },
      { id: 10, value: 10 },
      { id: 15, value: 15 }
    ];
    this.numberOfRowsInTable = { id: 10, value: 10 };
    this.filterFieldList = this.selection.fieldList();
    this.selectedFilterField = {
      field_name: "agent_plan_agent_name",
      name: "nombre",
      text: "nombre"
    };
    
     /****Seleccionar la fecha de hoy */
     this.selectedDate = textDateToObjectDate(moment().format("YYYY-MM-DD"));
  }

  ngOnInit() {
    this.onGetCurrentDate(this.selectedDate);
  }

  ngOnDestroy() {
    localStorage.setItem("recordSelection", "");
  }

  // IMPORT DATA FROM SERVICE
  getAll_Records(query?) {
    this.invAgentPlanService.getAllRecords(query).subscribe(
      (data: InvAgentPlanModel[]) => {
        let data_mapped = data.map(x => {
          return {
            agent_plan_id: x.agent_plan_id,
            agent_plan_agent_name: x.agent_plan_agent_name,
            agent_plan_agent_id: x.agent_plan_agent_id,
            agent_plan_date: (x.agent_plan_date).slice(0, 10),
            agent_plan_start_time: x.agent_plan_start_time,
            agent_plan_end_time: x.agent_plan_end_time,
            agent_plan_agent_type: x.agent_plan_agent_type
          };
        });

        this.rows = data_mapped;
        this.rows_original = data_mapped;

        this.alertMessage = new AlertModel();
        this.show_data = true;
      },
      error => {
        this.show_data = false;
        this.onError(error);
      }
    );
  }


  /********************************** */

  onGetCurrentDate(myDateObject) {
    /***Convertir data en string */
    let myDate = objectDateToTextDate(myDateObject);
    /***Construir el query */
    let status_field = "agent_plan_date";
    let query = `{"where":{"${status_field}":"${myDate}"}}`;
    /***Buscar registros */
    this.getAll_Records(query);
  }

  onDateChange(event?){
     /***Convertir data en string */
     let myDate = objectDateToTextDate(this.selectedDate);
    /***Construir el query */
    let status_field = "agent_plan_date";
    let query = `{"where":{"${status_field}":"${myDate}"}}`;
    /***Buscar registros */
    this.getAll_Records(query);
  }



//**************************************** */

  

  onGetAll($event?) {
    this.show_datatable = true;
    this.show_selected_button = false;
    this.selection = new InvAgentPlanModel();
    this.query = "";
    this.getAll_Records(this.query);
  }

 
  onItemListSelected(event?) {
    this.show_datatable = true;
    this.numberOfRowsInTable = event;
  }

  onFilterFieldList() {
    let temp = {
      lines: this.numberOfRowsInTable
    };
    this.action = { action: "numberOfRowsInTable", temp: temp };
    this.onGetAll();
  }

 

  onUpdateFilter(event) {
    this.show_datatable = true;
    let temp = getUpdateFilter(
      event,
      this.rows,
      this.rows_original,
      this.selectedFilterField.field_name
    );
    this.rows = temp;
  }

  onFindById(event?) {
    this.getSingle_Record(event);
    this.action.action = "selectedRecord";
    this.show_datatable = true;
  }

  // DETECT EVENTS ON DATATABLE
  onActivate(event) {}

  onSelect(event) {
    this.show_selected_button = true;
    this.selection = event.selected[0];
  }

  // ERROR
  onError(error?) {
    this.alertService.error(error.status);
    this.alertMessage.alertTitle = "Error del servidor";
    this.alertMessage.alertText = error.statusText;
    this.alertMessage.alertShow = true;
    this.alertMessage.alertClass =
      "alert alert-danger alert-dismissible fade show";
  }


  // IMPORT DATA FROM SERVICE
  getSingle_Record(event?) {
    this.rows = [event.temp];
    let id = event.temp.inv_agent_id;

    this.invAgentPlanService.getRecordById(id).subscribe(
      temp => {
        let data = [temp];
        this.rows_original = data;
        this.rows = data;
        this.action.temp = data;

        this.alertMessage = new AlertModel();
        this.show_data = true;
      },
      error => {
        this.show_data = false;
        this.onError(error);
      }
    );
  }

  onlistAnswer($event){}
}
